
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { PAYROLL_SERVICE, RPT205000511_REQ, RPT205000511_RES } from "@/shared/type/RPT205000511.type";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
import { ExportExcel } from "@/shared/services/ExportExcelService";

const excel = new ExportExcel()

export default defineComponent({
    name: 'RPT202001SS04',
    components: { 
        Pagination, 
        VueDatePicker 
    },

    data() {
        return {
            approvalRequestDate: "" as unknown,
            header: [
                { columnName: 'No' },
                { columnName: 'Reference No' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'From Account No' },
                { columnName: 'Beneficiary Account Number' },
                { columnName: 'Beneficiary Account Name' },
                { columnName: 'Account' },
                { columnName: 'Currency' },
                { columnName: 'feeAmount Charge' },
                { columnName: 'Remark' },
                { columnName: 'Status' },
                { columnName: 'Created by' },
                { columnName: 'Created date' },
                { columnName: 'Authorized by' },
                { columnName: 'Authorized Date' },
            ],
            table: [] as PAYROLL_SERVICE[],
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        }
    },
    
    mounted() {
        this.requestPayrollServiceDetail()
    },

    computed: {
        formattedDate(): string {
            return this.approvalRequestDate ? this.$datetime.formatVueDatePicker(this.approvalRequestDate as Date) : ''
        },
    },

    methods: {
        async requestPayrollServiceDetail() {
            const reqBody: RPT205000511_REQ = {
                approvalRequestDate: this.approvalRequestDate ? this.$datetime.convertDateToString(this.approvalRequestDate as Date) : '',
                pageSize: this.pageSize,
                pageNumber: this.currentPage,
            }
            const res = await this.$request.getRequest("RPT20500511", reqBody, false) as RPT205000511_RES
            this.table = res.body.list.map((item: PAYROLL_SERVICE) => {
                return {
                    approvalRequestDate: item.approvalRequestDate,
                    internetBankingApprovalNo: item.internetBankingApprovalNo,
                    serverTransactionID: item.serverTransactionID,
                    customerNo: item.customerNo,
                    customerName: item.customerName,
                    withdrawalAccountNo: item.withdrawalAccountNo,
                    receiverAccountNo: item.receiverAccountNo,
                    receiverName: item.receiverName,
                    transactionCurrencyCode: item.transactionCurrencyCode,
                    transactionAmount: this.$format.formatCurrency(item.transactionAmount, item.transactionCurrencyCode),
                    feeAmount: this.$format.formatCurrency(item.feeAmount, item.transactionCurrencyCode),
                    CorpBankingApprovalStatusCode: item.CorpBankingApprovalStatusCode ? `${item.CorpBankingApprovalStatusCode} : ${this.$codeConverter.convertValue("CorpBankingApprovalStatusCode", item.CorpBankingApprovalStatusCode)}` : '',
                    remark: item.remark,
                    firstRegisterTellerID: item.firstRegisterTellerID,
                    firstRegisterTellerName: item.firstRegisterTellerName,
                    firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                    approverID: item.approverID,
                    approverName: item.approverName,
                    authorizationDate: this.$datetime.formatStringToStringDate(item.authorizationDate)
                }
            })
            this.totalItems = res.body.totalCount
        },

        async exportToExcel() {
            const customHeader = [
                'Reference No',
                'Biz CID',
                'Biz Name',
                'From Account No',
                'Beneficiary Account Number',
                'Beneficiary Account Name',
                'Account',
                'Currency',
                'feeAmount Charge',
                'Remark',
                'Status',
                'Created by',
                'Created date',
                'Authorized by',
                'Authorized Date',
            ]
            const reqBody: RPT205000511_REQ = {
                approvalRequestDate: this.approvalRequestDate ? this.$datetime.convertDateToString(this.approvalRequestDate as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20500511", reqBody, false) as RPT205000511_RES
            const tableForExport = this.onMappingTableData(res.body.list)

			const dataForExport: ExportSheet = {
				data: tableForExport,
				columnHeader: [customHeader],
				sheetName: 'Payroll Service Report'
			}
			excel.exportSheet([dataForExport], 'Payroll Service Report')
        },

        onMappingTableData(lists) {
            return lists.map((item: PAYROLL_SERVICE) => ({
                serverTransactionID: item.serverTransactionID,
                customerNo: item.customerNo,
                customerName: item.customerName,
                withdrawalAccountNo: this.$format.formatAccountNumber(item.withdrawalAccountNo),
                receiverAccountNo: this.$format.formatAccountNumber(item.receiverAccountNo),
                receiverName: item.receiverName,
                transactionAmount: this.$format.formatCurrency(item.transactionAmount, item.transactionCurrencyCode),
                transactionCurrencyCode: item.transactionCurrencyCode,
                feeAmount: this.$format.formatCurrency(item.feeAmount, item.transactionCurrencyCode),
                remark: item.remark,
                CorpBankingApprovalStatusCode: `${item.CorpBankingApprovalStatusCode} : ${this.$codeConverter.convertValue("CorpBankingApprovalStatusCode", item.CorpBankingApprovalStatusCode)}`,
                firstRegisterTellerName: item.firstRegisterTellerName,
                firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                approverName: item.approverName,
                authorizationDate: this.$datetime.formatStringToStringDate(item.authorizationDate)
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.requestPayrollServiceDetail();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1
                this.isResetPage = true;
                this.requestPayrollServiceDetail()
            } else {
                this.isResetPage = true;
                this.requestPayrollServiceDetail()
            }
        },

        handleReset() {
            this.approvalRequestDate = "" as unknown
            this.currentPage = 1
            this.isResetPage = true
            this.requestPayrollServiceDetail()
        }
    }
})
